body {
    background-image: url("https://storage.needpix.com/rsynced_images/synthwave-3941721_1280.jpg");
}

#root {
    height: 100%;  
}

#drum-machine {
    height: 100%;
    width: 70vmax;
    margin: auto;
    display: flex;
    justify-content: center;
    align-items: center; 
}

#display {
    height: 20vmax;
    display: flex;
    flex-direction: row;
    justify-content: space-between;
    align-items: center;
}

.features {
    flex: 1 0 50%;
}

.keyPad {
    display: flex;
    flex-direction: row;
    flex-wrap: wrap;
}

.drum-pad-container {
    flex: 1 0 30%;
}

.drum-pad {
    width: 8em;
    height: 3.4em;
}

.ui.button {
    margin: 5px 0px 5px 0px;
}

.powerContainer {
    text-align: left;
}

.features {
    height: 100%;
}

.bank {
  margin-top: 15px;
  
}


input[type=range] {
    -webkit-appearance: none;
    margin: 18px 0;
    width: 100%;
  }
  input[type=range]:focus {
    outline: none;
  }
  input[type=range]::-webkit-slider-runnable-track {
    width: 100%;
    height: 8.4px;
    cursor: pointer;
    animate: 0.2s;
    box-shadow: 1px 1px 1px #000000, 0px 0px 1px #0d0d0d;
    background: #05ffa1;
    border-radius: 1.3px;
    border: 0.2px solid #010101;
  }
  input[type=range]::-webkit-slider-thumb {
    box-shadow: 1px 1px 1px #000000, 0px 0px 1px #0d0d0d;
    border: 1px solid #000000;
    height: 36px;
    width: 16px;
    border-radius: 3px;
    background: #ffffff;
    cursor: pointer;
    -webkit-appearance: none;
    margin-top: -14px;
  }
  input[type=range]:focus::-webkit-slider-runnable-track {
    background: #04e590;
  }
  input[type=range]::-moz-range-track {
    width: 100%;
    height: 8.4px;
    cursor: pointer;
    animate: 0.2s;
    box-shadow: 1px 1px 1px #000000, 0px 0px 1px #0d0d0d;
    background: #05ffa1;
    border-radius: 1.3px;
    border: 0.2px solid #010101;
  }
  input[type=range]::-moz-range-thumb {
    box-shadow: 1px 1px 1px #000000, 0px 0px 1px #0d0d0d;
    border: 1px solid #000000;
    height: 36px;
    width: 16px;
    border-radius: 3px;
    background: #ffffff;
    cursor: pointer;
  }
  input[type=range]::-ms-track {
    width: 100%;
    height: 8.4px;
    cursor: pointer;
    animate: 0.2s;
    background: transparent;
    border-color: transparent;
    border-width: 16px 0;
    color: transparent;
  }
  input[type=range]::-ms-fill-lower {
    background: #2a6495;
    border: 0.2px solid #010101;
    border-radius: 2.6px;
    box-shadow: 1px 1px 1px #000000, 0px 0px 1px #0d0d0d;
  }
  input[type=range]::-ms-fill-upper {
    background: #05ffa1;
    border: 0.2px solid #010101;
    border-radius: 2.6px;
    box-shadow: 1px 1px 1px #000000, 0px 0px 1px #0d0d0d;
  }
  input[type=range]::-ms-thumb {
    box-shadow: 1px 1px 1px #000000, 0px 0px 1px #0d0d0d;
    border: 1px solid #000000;
    height: 36px;
    width: 16px;
    border-radius: 3px;
    background: #ffffff;
    cursor: pointer;
  }
  input[type=range]:focus::-ms-fill-lower {
    background: #05ffa1;
  }
  input[type=range]:focus::-ms-fill-upper {
    background: #04e590;
  }


@media only screen
and (max-width : 515px) {
    body {
        background-color: #400080;
    }
    #root {
        overflow: hidden;
    }
    #display {
        height: 100vmax;
        flex-direction: column;
        align-items: center;
    }
    .features {
       display: flex;
       align-items: stretch;
    }
   
    #drum-machine {
        height: 100%;
        width: 100vmin;
        margin: 0;
        padding: 0;
    }
    .features {
        margin: 0;
        padding: 0;
    }
    .drum-pad {
        width: 7.5em;
        height: 5em;
    }
    .controlsContainer {
        display: flex;
        flex-direction: row;
        flex-wrap: wrap;
    }
    .controls {
        width: 100vmin;
    }
}

@media only screen 
  and (min-width: 768px) 
  and (max-width: 1024px) {
  .drum-pad {
    width: 6em;
  }
}


